<template>
  <div class="shop_category container align-items-center">
    <div class="row">
      <img
        src="https://img-cf.kurly.com/category/banner/pc/c7d4c66b-d0da-4486-a67c-c56d3c3dc69a"
        alt=""
      />
    </div>
    <div class="row">
      <div class="col">
        <div class="row category_top">
          <h1>{{ category }}</h1>
        </div>
        <div class="row category_lows">
          <ul
            class="list-group list-group-horizontal-sm flex-fill row row-cols-1 row-cols-sm-2 row-cols-md-5"
          >
            <li class="list-group-item ">친환경</li>
            <li class="list-group-item ">수입과일</li>
            <li class="list-group-item ">간편과일</li>
            <li class="list-group-item ">친환경</li>
            <li class="list-group-item ">수입과일</li>
            <li class="list-group-item ">간편과일</li>
            <li class="list-group-item ">간편과일</li>
          </ul>
        </div>
      </div>
      <div class="row">
        <div class="col">정렬버튼</div>
      </div>
      <div class="list_goods row">
        <div class="row">
          <ul class="list row row-cols-2 row-cols-sm-2 row-cols-md-3">
            <li
              v-for="(item, index) in goodsList"
              :key="index"
              class="list-group-item "
            >
              <div class="item">
                <div class="row thumb">
                  <img
                    class="items_iamge"
                    v-bind:src="item.item_image"
                    alt=""
                  />
                  <div class="group_btn">
                    <button>장바구니/좋아요</button>
                  </div>
                </div>
                <div class="row info">
                  <span class="title">{{ item.item_title }}</span>
                  <span class="cost">{{ item.item_price }}</span>
                  <span class="content">{{ item.item_content }}</span>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { list } from "./test"

export default {
  name: "ShopCategory",
  data() {
    return {
      categoryNum: this.$route.params.category,
      category: "",
      goodsList: list,
    }
  },
  watch: {
    "$route.params.category": function(current) {
      this.changeCate(current)
    },
  },
  created() {
    this.changeCate(this.$route.params.category)
  },
  methods: {
    changeCate(param) {
      if (param == 1) {
        this.category = "과일"
      } else if (param == 2) {
        this.category = "채소"
      } else if (param == 3) {
        this.category = "쌀/잡곡"
      } else if (param == 4) {
        this.category = "축산/계란"
      }
    },
  },
}
</script>

<style scoped>
.shop_category {
  margin-top: 54px;
}

.category_lows {
  height: 100px;
  border: 1px solid #e2e2e2;
  box-sizing: border-box;
}

.category_lows ul {
  margin: 0;
  padding: 0;
}

.list-group-item {
  border: none;
}

.thumb {
  position: relative;
}

.items_iamge {
  width: 100%;
}

.group_btn {
  position: absolute;
  z-index: 2;
  right: 15px;
  bottom: 15px;
  width: fit-content;
}
</style>
